.chatInput {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	height: 42px;
}

.chatInput input {
	-ms-flex: 1 1;
	    flex: 1 1;
	height: 100%;
	border: 0;
	padding: 0 15px;
}

.chatInput button {
	-ms-flex: 0 1;
	    flex: 0 1;
	height: 100%;
	padding: 0 20px;
	border: none;
	background: green;
	color: white;
	font-size: 18px;
	text-align: center;
	-webkit-transition: .5s;
	-o-transition: .5s;
	transition: .5s;
}

.chatInput button:hover {
	cursor: pointer;
	opacity: .8;
}


